.header {
  background-image: url("../images/header-bg.png");
  background-size: cover;
  background-position: 85% 50%;

  @include on-tablet {
    background-position: center;
  }

  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    letter-spacing: -1.5px;
    white-space: nowrap;

    @include on-tablet {
      font-size: 64px;
    }
  }

  &__bottom {
    padding-bottom: 88px;

    @include on-tablet {
      padding-bottom: 64px;
    }
  }
}
