.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #e5e5e5;
  font-weight: 600;
  text-transform: uppercase;
  overflow: hidden;
  padding-right: 12px;

  &__top {
    margin-bottom: 24px;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #e5e5e5;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 16px;
    line-height: 27px;
    color: $menu-text-color;
    text-decoration: none;
    width: fit-content;
    &:hover {
      color: $hover-color;
    }
  }

  &__book-a-text-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $menu-text-color;

    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: $menu-text-color;
    text-decoration: none;
    &:hover {
      color: $hover-color;
    }
  }
}
