.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-size: cover;
  transition: transform 0.3s;
  position: relative;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    height: 32px;
    width: 32px;

  }

  &--phone {
    background-image: url(../images/icons/Icon-Phone-call-2x.png);
    &:hover::before {
      position: absolute;
      content: "+1 234 555-55-55";
      font-size: 12px;
      font-weight: 700;
      line-height: 16px;
      letter-spacing: 2px;
      color: $main-text-color;
      right: 100%;
      width: max-content;
      padding-right: 20px;
      transform: translateY(50%);
      white-space: nowrap;
    }
  }

  &--menu {
    background-image: url(../images/icons/Icon-Burger-menu-2x.png);

    @include transition-page;
    &:hover {
      background-image: url(../images/icons/Icon-Burger-menu-hover-2x.png);
    }
  }

  &--close {
    background-image: url(../images/icons/Icon-Close-2x.png);
  }
}
